.zmxl {
    .bcg::after {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-image: url(http://newsimg.5054399.com/uploads/userup/2207/ertong_f4ffcb238e111f49aa5080e94bf26876.jpg);
        background-repeat: no-repeat;
        background-size: 100%;
        // background-attachment: fixed;
        opacity: 0.25;
        z-index: -100;
    }

    h2 {
        color: #000;
        font-size: 28px;
        text-align: left;
        padding-bottom: 15px;
        padding-left: 100px;
    }

    a {
        padding-left: 60px;
        font-size: 15px;
    }

    a:hover {
        color: rgb(24, 28, 70);
    }

    p {
        color: #000;
        font-size: 15px;
        text-align: left;
        width: 1500px;
        padding-bottom: 15px;
        padding-left: 150px;
        line-height: 26px;
        text-indent: 2em;
    }

    .photo {
        position: relative;
        left: 10vw;
        width: 1200px;
        padding-bottom: 100px;
    }
}