* {
  margin: 0;
  padding: 0;
}
body,
html {
  height: 100%;
}
html {
  scroll-padding-top: 47px;
}
.container .topmenu {
  position: fixed;
  width: 100%;
  height: 47px;
  display: inline;
  text-align: center;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 999;
}
.container #rightdata {
  margin-top: 47px;
}
.container .ant-carousel .slick-dots li button {
  height: 24px;
  width: 24px;
  border-radius: 12px;
}
.container .ant-carousel .slick-dots li {
  width: 24px;
  height: 24px;
  border: 4px solid transparent;
  border-radius: 36px;
}
.container .ant-carousel .slick-dots li.slick-active {
  width: 24px;
  height: 24px;
  border: 4px solid #0f87e8;
  border-radius: 24px;
}
