.War3 {
    .bcg::after {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-image: url(https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fimg.app178.com%2Ftu%2F201410%2Fhyivcksj2mn.jpg&refer=http%3A%2F%2Fimg.app178.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=auto?sec=1668524679&t=aa6e534ee6dfbd6bfc2e8eafe9abbb81);
        background-repeat: no-repeat;
        background-size: 100%;
        // background-attachment: fixed;
        opacity: 0.3;
        z-index: -100;
    }
    h2 {
        color: #000;
        font-size: 28px;
        text-align: left;
        padding-bottom: 15px;
        padding-left: 100px;
    }

