        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-image: url(https://pic2.7fgame.com/qfcms/20220107/5e497a9c2fe24f668f782c4f985e6d63.jpg);
        background-repeat: no-repeat;
        background-size: 100%;
        // background-attachment: fixed;
        opacity: 0.3;
        z-index: -100;
