  display: block;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: url(http://newsimg.5054399.com/uploads/userup/2207/ertong_f4ffcb238e111f49aa5080e94bf26876.jpg);
